:root {
  /* Переменные для темной темы (по умолчанию) */
  --background-color: #000;
  --card-background: #101010;
  --text-color: #fff;
  --text-secondary: rgba(255, 255, 255, 0.7);
  --border-color: rgba(255, 255, 255, 0.1);
  --tile-background: #0f0f0f;
  --hover-color: #1a1a1a;
  --badge-background: rgba(255, 255, 255, 0.05);
}

/* Переменные для светлой темы */
[data-theme="light"] {
  --background-color: #f5f5f7;
  --card-background: #ffffff;
  --text-color: #000000;
  --text-secondary: rgba(0, 0, 0, 0.7);
  --border-color: rgba(0, 0, 0, 0.1);
  --tile-background: #f0f0f2;
  --hover-color: #e8e8e8;
  --badge-background: rgba(0, 0, 0, 0.05);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  background-color: var(--background-color);
  color: var(--text-color);
}

.card {
  background-color: var(--card-background);
  border: 1px solid var(--border-color);
  color: var(--text-color);
}

.skill-card, .info-tiles > div, .view-all-skills {
  background-color: var(--tile-background) !important;
  border: 1px solid var(--border-color) !important;
}

.badge {
  background-color: var(--badge-background);
}

.file-name, .file-header, .section-title {
  color: var(--text-color);
}

.skill-status, .skills-description {
  color: var(--text-secondary);
}

.action-button {
  background-color: var(--tile-background);
  border: 1px solid var(--border-color);
  color: var(--text-color);
}

.action-button:hover {
  background-color: var(--hover-color);
}

/* Обновляем иконки и другие элементы для светлой темы */
[data-theme="light"] svg {
  color: var(--text-color);
}

[data-theme="light"] .skill-card:hover,
[data-theme="light"] .info-tiles > div:hover,
[data-theme="light"] .action-button:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
}

/* Дополнительные стили для анимации перехода темы */
.app, .card, .skill-card, .info-tiles > div, .badge, .action-button, .view-all-skills {
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease;
}
